<script>
  module.exports = {
    data(){
      return {
      	closeshop:{
          value:"",
          isdata:"",
          options:[{"storeCode":1,storeName:"sss"}]

        }
      }
    },
    methods: {
      changeend(l1,l2){ //选择完之后的回调函数

        console.log("回调storeName："+l1)
