                <span class="hljs-string">"categoryCode"</span>:<span class="hljs-string">"R0301002"</span>, <span class="hljs-comment">// 线下四级品类代码</span>
                <span class="hljs-string">"commerceItemId"</span>:<span class="hljs-string">"45574404"</span>,<span class="hljs-comment">//商品唯一号</span>
                <span class="hljs-string">"installCount"</span>:<span class="hljs-string">"1"</span>,<span class="hljs-comment">//安装数量</span>
