                <span class="hljs-string">"skuNo"</span>:<span class="hljs-string">"100253548"</span>,<span class="hljs-comment">//商品编码</span>
                <span class="hljs-string">"townCode"</span>:<span class="hljs-string">"110102002"</span>,<span class="hljs-comment">//四级区域</span>
