<template><section>
<h2>g-header 展示</h2>
<p>常用于中台页面公共头展示</p>
<h3>基础用法</h3>
<p>头部显示以下内容<br>
支持自定义tittle<br>
支持自定义头部样式<br>
支持插入slot<br></p>
<div class="demo-box">
  <div class="demo-block">
    <g-header @clickClose="clickClose" v-model="initheader">
      <!-- <template slot="child-nav">需要的话自己加内容</template> -->
    </g-header>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head><template>
   <g-header @clickclose="clickClose" v-model="initheader">
    <!-- <template slot="child-nav">需要的话自己加内容</template> -->
    </g-header>
</template>

</head><body></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">g-header</span>  <span class="hljs-attr">@clickClose</span>=<span class="hljs-string">"clickClose"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"initheader"</span>&gt;</span>
    <span class="hljs-comment">&lt;!-- &lt;template slot="child-nav"&gt;需要的话自己加内容&lt;/template&gt; --&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">g-header</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
   data(){
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">initheader</span>:{
          <span class="hljs-string">"curstyle"</span>:{  <span class="hljs-comment">//自己定义样式</span>
              color:<span class="hljs-string">"yellow"</span>
                    },
            <span class="hljs-string">"showclose"</span>:<span class="hljs-string">"X"</span>,  <span class="hljs-comment">//右面的关闭</span>
            <span class="hljs-string">"content"</span>:<span class="hljs-string">"这个是自己定义的名字"</span> <span class="hljs-comment">//tit名字</span>
        }
      }
    },
    <span class="hljs-attr">methods</span>: {
      clickClose(){
        alert(<span class="hljs-string">'点击返回执行对应操作'</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>curstyle</td>
<td>头部样式</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>showclose</td>
<td>头部关闭按钮展示</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>content</td>
<td>头部tittle展示</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>clickClose</td>
<td>返回按钮或者关闭当前页面的回调操作</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  module.exports = {
    data(){
      return {
        initheader:{
          "curstyle":{  //自己定义样式
              color:"yellow"
                    },
            "showclose":"X",  //右面的关闭
            "content":"这个是自己定义的名字" //tit名字
        }
      }
    },
    methods: {
      clickClose(){
        alert('点击返回执行对应操作')
      }
    }
  };
</script>


