<template><section><h1>快速上手</h1>
<hr>
<h2>使用前准备</h2>
<blockquote>
<p>在使用之前，推荐学习 <code v-pre="" class="code_inline">Vue</code> 和 <code v-pre="" class="code_inline">ES2016</code> ，并正确配置 <code v-pre="" class="code_inline">Node.js</code> v6.x 或以上版本</p>
</blockquote>
<p><code v-pre="" class="code_inline">vg-unit</code> 基于 <code v-pre="" class="code_inline">Vue.js</code> 2.x+ 版本开发，所以有必要了解以下基础知识：</p>
<ul>
<li><a href="https://cn.vuejs.org/v2/guide/components.html">Vue 组件</a></li>
<li><a href="https://cn.vuejs.org/v2/guide/single-file-components.html">单文件组件</a></li>
</ul>
<h2>基于模板工程开发</h2>
<blockquote>
<p><code v-pre="" class="code_inline">Vue.js</code> 提供一个官方命令行工具 <code v-pre="" class="code_inline">vue-cli</code>，可用于快速搭建大型单页应用。该工具提供开箱即用的构建工具配置，带来现代化的前端开发流程。只需几分钟即可创建并启动一个带热重载、保存时静态检查以及可用于生产环境的构建配置的项目。</p>
</blockquote>
<pre v-pre=""><code v-pre="" class="language-bash">&gt; npm i -g vue-cli

 &gt; mkdir my-project &amp;&amp; <span class="hljs-built_in">cd</span> my-project

 &gt; vue init webpack

 &gt; npm i &amp;&amp; npm i vg-unit --save
</code></pre>
<h2>标准开发</h2>
<p>实际项目中，往往会使用 <code v-pre="" class="code_inline">webpack</code>，<code v-pre="" class="code_inline">rollup</code> 或者 <code v-pre="" class="code_inline">gulp</code> 的工作流，大多可以做到按需加载页面用到的组件，所以不推荐直接使用 <code v-pre="" class="code_inline">&lt;script&gt;</code> 标签全局引入的方式使用。</p>
<h3>全局组件使用</h3>
<p>可以在项目的入口文件中引入所有组件或所需组件</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-keyword">import</span> Vgunit <span class="hljs-keyword">from</span> <span class="hljs-string">'vg-unit'</span> <span class="hljs-comment">// 引入组件库</span>

 <span class="hljs-keyword">import</span> <span class="hljs-string">'vg-unit/packages/theme-default/lib/index.css'</span> <span class="hljs-comment">// 引入样式库</span>

 Vue.use(Vgunit)
</code></pre>
<h3>单个组件按需使用</h3>
<p>可以局部注册所需的组件，适用于与其他框架组合使用的场景</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-keyword">import</span> { Gheader } <span class="hljs-keyword">from</span> <span class="hljs-string">'vg-unit'</span>

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {

  <span class="hljs-attr">components</span>: {

    Gheader
  }
}
</code></pre>
<p>在模板中，用 <code v-pre="" class="code_inline">&lt;g-header @clickClose=&quot;clickClose&quot; v-model=&quot;initheader&quot;&gt; 			&lt;!-- &lt;template slot=&quot;child-nav&quot;&gt; &lt;/template&gt;	 --&gt;	// 需要的话自己加内容	 			&lt;/g-header&gt;</code> 自定义标签的方式使用组件</p>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    	<span class="hljs-tag">&lt;<span class="hljs-name">g-header</span>  <span class="hljs-attr">@clickClose</span>=<span class="hljs-string">"clickClose"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"initheader"</span>&gt;</span>
		<span class="hljs-comment">&lt;!-- &lt;template slot="child-nav"&gt; &lt;/template&gt;	 --&gt;</span>	// 需要的话自己加内容	
	<span class="hljs-tag">&lt;/<span class="hljs-name">g-header</span>&gt;</span>

  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
	<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
	    data() {
	      <span class="hljs-keyword">return</span>{
	      	<span class="hljs-string">"initheader"</span>:{ 
			<span class="hljs-string">"curstyle"</span>:{ },
			<span class="hljs-string">"showclose"</span>:<span class="hljs-string">"X"</span>,  
			<span class="hljs-string">"content"</span>:<span class="hljs-string">"自己取名字"</span> 
			}
	      }
	     }
	}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</section></template>

<script>
	export default {
	    data() {
	      return{
	      	"initheader":{ 
			"curstyle":{ },
			"showclose":"X",  
			"content":"自己取名字" 
			}
	      }
	     }
	}
</script>


